import Vue from 'vue'
import axios from 'axios'
import VueLazyLoad from 'vue-lazyload'
import { Toast } from 'vant';

Vue.use(Toast);
Vue.use(VueLazyLoad,{
  loading:require('../../../common/images/loading.jpg')
})
export default {
  name: "index",
  data() {
    return {
      isLoading:false,//开始下拉刷新
      keywords: '', //关键字
      SwipeImg: [  //轮播图
        require('../images/s01.jpg'),
        require('../images/s02.jpg'),
        require('../images/s03.jpg'),
        require('../images/s04.jpg')
      ],
      MenuList: [  //菜单
        {
          src: require('../icon/m01.jpg'),
          text:'抗感冒药'
        },
        {
          src: require('../icon/m02.jpg'),
          text:'清热解毒类'
        },
        {
          src: require('../icon/m03.jpg'),
          text:'呼吸系统类'
        },
        {
          src: require('../icon/m04.jpg'),
          text:'皮肤类'
        },
        {
          src: require('../icon/m05.jpg'),
          text:'胃肠道类'
        },
        {
          src: require('../icon/m06.jpg'),
          text:'抗生素'
        },
        {
          src: require('../icon/m07.jpg'),
          text:'男性用药'
        },
        {
          src: require('../icon/m08.jpg'),
          text:'妇科用药'
        },
        {
          src: require('../icon/m09.jpg'),
          text:'滋补强身类'
        },
        {
          src: require('../icon/m10.jpg'),
          text:'消化系统类'
        },
        {
          src: require('../icon/m11.jpg'),
          text:'肝胆科类'
        },
        {
          src: require('../icon/m12.jpg'),
          text:'外用药类'
        },
        {
          src: require('../icon/m13.jpg'),
          text:'心脑血管系统类'
        },
        {
          src: require('../icon/m14.jpg'),
          text:'儿科药类'
        },
        {
          src: require('../icon/m15.jpg'),
          text:'营养保健'
        },
        {
          src: require('../icon/m16.jpg'),
          text:'眼科用药'
        },
      ],
      ProductList:[], //产品列表
      loadFinsh:true //列表数据加载完毕
    }
  },
  methods: {
    // 进入详情
    dj(item){
      this.$store.commit('DATA',item);
      this.$router.push('/details')
    },
    getList(){
      Toast.loading({
        message: '加载中...',
        forbidClick: this.loadFinsh
      });
      axios.get('http://api2.mall.xinquanjk.com/hotSaleGoods/getList?size=30',{
        //data 参数

      }).then(res => {
        if(res.status == 200){
          this.ProductList = res.data.data.info.records;
          this.loadFinsh = false;//数据加载完毕，隐藏“正在加载中...”
          this.$store.commit('SHOP', this.ProductList)
        }
      }).catch(e => {
        console.log(e);
      })
    },
    onRefresh() {
      setTimeout(() => {
        this.$toast('刷新成功');
        this.isLoading = false;
      }, 500);
    },
    onLoad(){
      console.log("上拉加载更多！");
    }
  },
  created(){
    //获取列表数据
    this.getList();
  }
}



